<template>
  <el-container class="doctor_contanier">
    <el-header>
      <div>
        <img src="../assets/logo.png" alt="">
        <span>医疗信息存储系统(医生端)</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu background-color="#545c64" text-color="#fff" active-text-color="#409fff" :unique-opened="true" >
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-user-solid"></i>
              <span>个人信息</span>
            </template>
            <el-menu-item index="1-1">
              <template slot="title">
                <i class="el-icon-s-tools"></i>
                <span>查询个人信息</span>
              </template>
            </el-menu-item>
            <el-menu-item index="1-2">
              <template slot="title">
                <i class="el-icon-s-tools"></i>
                <span>修改个人信息</span>
              </template>
            </el-menu-item>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-s-order"></i>
              <span>处理病例</span>
            </template>
            <el-menu-item index="2-1">
              <template slot="title">
                <i class="el-icon-s-claim"></i>
                <span>新建病例</span>
              </template>
            </el-menu-item>
            <el-menu-item index="2-2">
              <template slot="title">
                <i class="el-icon-s-claim"></i>
                <span>更新病例</span>
              </template>
            </el-menu-item>
          </el-submenu>
          <el-menu-item index="3">
            <i class="el-icon-s-promotion"></i>
            <span slot="title">申请授权</span>
          </el-menu-item>
          <el-menu-item index="4">
            <i class="el-icon-s-check"></i>
            <span slot="title">查看授权</span>
          </el-menu-item>
        </el-menu>
        </el-aside>
      <el-main>Main</el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data () {
    return {
      // 左侧菜单
      menulist: []
    }
  },
  created () {
    this.getMenuList()
  },
  methods: {
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    }
  },
  getMenuList () {
    this.$axios.get('menus').then(res => {
      if (res.meta.status !== 200) return this.$message.err(res.meta.msg())
      this.menulist = res.data
      console.log(res)
    })
  }
}
</script>

<style lang='less' scoped>
.doctor_contanier {
  height: 100%;
}
.el-header {
  background-color: #2E8B57;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #eaedf1;
  font-size: 22px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 2px;
    }
  }
}
.el-aside {
  background-color: #548B54;
  .el-menu {
    border-right: none;
  }
}
.el-main {
  background-color: #eaedf1;
}
</style>
