<template>
  <div class="login_container">
    <div class="login_box">
      <!--头像-->
      <div class="avater_box">
        <img src="../assets/logo.png" alt="">
      </div>
      <!--登录表单-->
      <el-form ref="loginformRef" label-width="0px" class="login_form" :model="loginForm" :rules="loginFormrules">
        <!--用户名-->
        <el-form-item  prop="userphone">
          <el-input placeholder="请输入手机号码" prefix-icon="el-icon-user" v-model="loginForm.userphone"></el-input>
        </el-form-item>
        <!--密码-->
        <el-form-item prop="password">
          <el-input placeholder="请输入密码"  prefix-icon="el-icon-lock" v-model="loginForm.password" type="password"></el-input>
        </el-form-item>
        <!--身份选择-->
        <el-form-item label="身份" style="padding-left: 30px" label-width="60px" prop="userType">
          <el-radio-group v-model="loginForm.userType" >
            <el-radio label="医生" value="1"></el-radio>
            <el-radio label="患者" value="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <!--按钮-->
        <el-form-item class="btns">
          <el-button type="success" @click="submitForm" plain>登录</el-button>
          <el-button type="info" @click="resetLoginform" plain>重置</el-button>
          <el-button type="text">注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {

  data () {
    return {
      loginForm: {
        userphone: '15737275017',
        password: '123456',
        userType: '医生'
      },
      loginFormrules: {
        userphone: [
          { required: true, message: '请输入电话号码', trigger: 'blur' },
          { min: 11, max: 11, message: '请长度为11位的电话号码', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 8, message: '长度在 6 到 8 个字符', trigger: 'blur' }
        ],
        userType: [
          { required: true, message: '请选择一个', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    resetLoginform () {
      this.$refs.loginformRef.resetFields()
    },
    submitForm () {
      this.$refs.loginformRef.validate((valid) => {
        if (valid) {
          this.$axios.post('http://192.168.0.134:8090/medical/user/login', this.loginForm).then(res => {
            if (res.data.status === 200) {
              alert('登陆成功!')
            }
            console.log(res)
            window.sessionStorage.setItem('token', res.data.token)
            if (this.userType === '医生') {
              this.$router.push('/doctor')
            } else {
              this.$router.push('patient')
            }
          })
        } else {
          console.log('登陆失败!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="less">
.login_container{
  background-color: #2B4B6B;
  height: 100%;
}
.login_box{
  width: 450px;
  height: 400px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  .avater_box{
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #fff;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}

</style>
